body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  .ielts-writing-page {
    flex: 1;
    display: flex;
    flex-direction: column;
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    background-color: #f8f9fa;
    position: relative;
    padding-bottom: 80px; /* Add padding to prevent content overlap with footer */
  }
  
  .main-header {
    text-align: center;
    background-color: #e9ecef;
    padding: 10px 0;
    margin-bottom: 20px;
  }
  
  .main-header h1 {
    margin: 0;
    font-size: 24px;
    font-weight: bold;
    color: #333;
  }
  
  .header {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .timer {
    font-size: 16px;
    font-weight: bold;
    color: #333;
  }
  
  .content {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .question-panel {
    width: 45%;
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .chart-image {
    max-width: 100%;
    height: auto;
    margin-top: 20px;
  }
  
  .answer-panel {
    width: 45%;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .answer-textarea {
    width: 100%;
    height: auto;
    font-size: 18px;
    line-height: 1.5;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: none;
    overflow: hidden;
    min-height: 180px; /* Default height of 10 lines */
  }
  
  .word-counter {
    margin-top: 10px;
    font-size: 14px;
    color: #666;
    text-align: right;
  }
  
  .footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #f8f9fa;
    border-top: 1px solid #ddd;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.1);
    z-index: 1000; /* Ensure the footer is on top */
  }
  
  .navigation {
    display: flex;
    gap: 10px;
  }
  
  .nav-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .nav-button:disabled {
    background-color: #aaa;
    cursor: not-allowed;
  }
  
  .nav-button:hover:not(:disabled) {
    background-color: #0056b3;
  }
  
  .submit-button {
    display: flex;
  }
  
  .submit-button button {
    padding: 10px 20px;
    margin-right: 50px;
    font-size: 16px;
    border: none;
    background-color: #28a745;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .submit-button button:hover {
    background-color: #218838;
  }
  
  /* Instruction Page Styles */
  .instructions {
    text-align: center;
    padding: 20px;
    background-color: #f8f9fa;
    border: 1px solid #ddd;
    border-radius: 4px;
    max-width: 700px;
    margin: 0 auto;
  }
  
  .instructions h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .instructions p {
    font-size: 16px;
    color: #555;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .instructions ul {
    list-style-type: disc;
    text-align: left;
    padding-left: 40px;
    margin-bottom: 20px;
  }
  
  .instructions button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .instructions button:hover {
    background-color: #0056b3;
  }
  
  /* Completion Page Styles */
  .completion {
    text-align: center;
    padding: 50px;
    background-color: #f8f9fa;
    border: 1px solid #ddd;
    border-radius: 4px;
    max-width: 600px;
    margin: 100px auto;
  }
  
  .completion h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 20px;
  }
  
  .completion p {
    font-size: 16px;
    color: #555;
    margin-bottom: 20px;
  }
  